import {Button, Modal} from "antd";

const TableModal = (props) => {

    const handleOk = () => {
        props.handleOk();
    };

    const handleCancel = () => {
        props.handleCancel();
    };

    return (
        <Modal title="Описание роли" visible={props.isModalVisible} onOk={handleOk} onCancel={handleCancel} footer={[
            <Button key="submit" type="primary" onClick={handleOk}>
                OK
            </Button>,
        ]}>
            <p>{props.roleRules}</p>
        </Modal>
    )
}

export default TableModal;