import {Button, InputNumber, Space} from "antd";
import './PlayersQty.css'
import Title from "antd/es/typography/Title";
import {useState} from "react";

const PlayersQty = (props) => {
    const [inpPlayersQty, setInpPlayersQty] = useState(2);

    const inputOnChange = (val) => {
        setInpPlayersQty(val);
    }
    const btnOnClick = () => {
        props.updPQ(inpPlayersQty);
        props.setPlayersQtyVisible(false);
    }

    return (
        <div className="container container--qty">
            <div className={"players-qty"}>
                <div className="players-qty__inner">
                    <Title>Количество игроков</Title>
                    <Space direction="vertical" align="center" size="large">
                        <InputNumber min="2" max="10" className="players-qty__input" value={inpPlayersQty} onChange={inputOnChange}/>
                        <Button type="primary" size="large" onClick={btnOnClick}>GO</Button>
                    </Space>
                </div>
            </div>
        </div>
    )
}

export default PlayersQty;