import {Button, Col, Row} from "antd";
import TableModal from "../TableModal/TableModal";
import RolesTable from "../RolesTable/RolesTable";
import {useState} from "react";

const RolesTableWrapper = (props) => {

    const [isModalVisible, setIsModalVisible] = useState(false);

    const [roleRules, setRoleRules] = useState('...')
    // const roles = ['Варвар', 'Танк', 'Некромант', 'Ядовитый', 'Дамагер', 'Плут', 'Отражатель'];
    const roles = [
        {
            id: 0,
            name: 'Варвар',
            roleRules: '9 карт, отбивается 1 раз за игру, нельзя вернуть в игру'
        },
        {
            id: 1,
            name: 'Танк',
            roleRules: '6 карт, может отбить одной картой две'
        },
        {
            id: 2,
            name: 'Некромант',
            roleRules: '7 карт, воскрешает 2 карты из отбоя в руку сопернику 2 раза за игру'
        },
        {
            id: 3,
            name: 'Ядовитый',
            roleRules: '7 карт, делает укол с ядом 2 раза за игру'
        },
        {
            id: 4,
            name: 'Дамагер',
            roleRules: '6 карт, может подкидывать +- 1 карту по номиналу'
        },
        {
            id: 5,
            name: 'Плут',
            roleRules: '7 карт, два козыря'
        },
        {
            id: 6,
            name: 'Отражатель',
            roleRules: '7 карт, отражает карту 2 раза за игру'
        }
    ];

    const showModal = (e) => {
        setIsModalVisible(true);
        const roleId = e.target.dataset.id
        setRoleRules(roles[roleId].roleRules);
    };

    const handleOk = () => {
        setIsModalVisible(false);
    };

    const handleCancel = () => {
        setIsModalVisible(false);
    };

    const btnOnClick = () => {
        props.setPlayersQtyVisible(true);
    }

    return (
        <div className="container container--main">
            <Row justify="center">
                <Col xs={24} lg={8} align="center">
                    <RolesTable playersQty={props.playersQty} showModal={showModal} roles={roles}/>
                    <br />
                    <Button type="primary" size="large" onClick={btnOnClick}>Новая партия</Button>
                </Col>
            </Row>
            <TableModal isModalVisible={isModalVisible} handleOk={handleOk} handleCancel={handleCancel} roleRules={roleRules}/>
        </div>
    )
}

export default RolesTableWrapper;