import './App.css';
import PlayersQty from "./PlayersQty/PlayersQty";
import RolesTableWrapper from "./RolesTableWrapper/RolesTableWrapper";
import {useState} from "react";


function App() {
    const [playersQty, setPlayersQty] = useState(0);
    const [playersQtyIsVisible, setPlayersQtyVisible] = useState(true);

    const updPQ = (inpPlayersQty) => {
        setPlayersQty(inpPlayersQty);
    }

    return (
          <main>
              {playersQtyIsVisible ?
                  <PlayersQty updPQ={updPQ} setPlayersQtyVisible={setPlayersQtyVisible}/> :
                  <RolesTableWrapper playersQty={playersQty} setPlayersQtyVisible={setPlayersQtyVisible} />
              }
          </main>
    );
}

export default App;
